import { selectConfigCopyRight } from '@containers/Home/selectors';
import React, { memo } from 'react';
import { useSelector } from 'react-redux';

const ControlbarCopyright = memo(() => {
  const copyRight = useSelector(selectConfigCopyRight);

  return (
    <div className="copyright">
      {copyRight?.isEnabled && (
        <p>
          {copyRight?.description}{' '}
          {copyRight?.link ? (
            <a target="_blank" href={copyRight.link} rel="noreferrer">
              <span>{copyRight?.author}</span>
            </a>
          ) : (
            copyRight?.author
          )}
        </p>
      )}
    </div>
  );
});

export default ControlbarCopyright;
